import React, { Component } from "react";

import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { InputDate, ReanderSelect } from "../../../../components/helpers/field";
import {
  getTglSystem,
} from "../../../../components/helpers/function";
import Tabel from "../../../../components/helpers/tabel";
import ValidasiTanggalLaporan from "../../../../Validasi/ValidasiTanggalLaporan";
// import LaporanExelPenjualan from "./exel/LaporanExelPenjualan";
// import LaporanBatalServiceExel from "./exel/LaporanBatalServiceExel";
// import ExportTransaksiPenjualan from "./pdf/ExportTransaksiPenjualan";
import Loading from "react-fullscreen-loading";
import { GetDataGroup } from "../../../../actions/datamaster_action";
import LaporanMarginPdf from "./pdf/LaporanMarginPdf";
import LaporanExelMargin from "./exel/LaporanExelMargin";

class HeadLaporanMargin extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
      type_laporan: "SEMUA",
      databarang: [],
      columns : [
        {
          dataField: "kode_barcode",
          text: "Kode Barcode",
          footerAttrs: {
            colSpan: "3",
          },
          footer : ""
        },
        {
          dataField: "nama_barang",
          text: "Nama Barang",
        },
        {
          dataField: "kode_group",
          text: "Kode Group",
        },
        {
          dataField: "modal",
          text: "Modal",
          headerClasses: "text-right",
          footer: (cell, row, num, index) => {
            return (
              <div className="text-right">
                {cell.reduce((a, b) => a + b, 0).toLocaleString("kr-KO")}
              </div>
            );
          },
          formatter: (cell) => {
            return (
              <div className="text-right">{cell.toLocaleString("kr-KO")}</div>
            );
          },
        },
        
        {
          dataField: "di_jual",
          text: "Dijual",
          headerClasses: "text-right",
          footer: (cell, row, num, index) => {
            return (
              <div className="text-right">
                {cell.reduce((a, b) => a + b, 0).toLocaleString("kr-KO")}
              </div>
            );
          },
          formatter: (cell) => {
            return (
              <div className="text-right">{cell.toLocaleString("kr-KO")}</div>
            );
          },
        },
        
        {
          dataField: "margin",
          text: "Margin",
          headerClasses: "text-right",
          footer: (cell, row, num, index) => {
            return (
              <div className="text-right">
                {cell.reduce((a, b) => a + b, 0).toLocaleString("kr-KO")}
              </div>
            );
          },
          formatter: (cell) => {
            return (
              <div className="text-right">{cell.toLocaleString("kr-KO")}</div>
            );
          },
        },
      ]
    };
    // this.handleSubmit = this.handleSubmit.bind(this);
    this.setStartDate = this.setStartDate.bind(this);
    this.setLastDate = this.setLastDate.bind(this);
  }

  componentDidMount() {
    this.props.change("type", "SEMUA");
    this.props.change("kode_group", "ALL");
    this.props.change("jenis_group", "ALL");
    this.props.dispatch(GetDataGroup());

  }

  exportPdf() {
    LaporanMarginPdf(this.props.LaporanDataPenjualanMargin)
  }
  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    this.setState({
      tgl_akhir: new Date(date),
    });
  }

  typeLaporan(e) {
    if (e === "SEMUA") {
      this.setState({
        type_laporan: "SEMUA",
      });
    } else {
      this.setState({
        type_laporan: "SELISIH",
      });
    }
  }

  render() {
    let datagroup = [
      {
        value : "ALL",
        name : "SEMUA"
      }
    ]
    this.props.GetDataGroup.forEach(list => {
      let row = {
        name : list.kode_group,
        value : list.kode_group,
      }
      datagroup.push(row) 
    });
    
    
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off">
        <div className="row">
          <div className="col-lg-3">
            <Field
              name="tgl_awal"
              component={InputDate}
              label="Tanggal Dari"
              type="text"
              selected={this.state.tgl_awal}
              onChange={(date) => this.setStartDate(date)}
              placeholder="Masukan Tanggal Dari"
            />
          </div>

          <div className="col-lg-3">
            <Field
              name="tgl_akhir"
              component={InputDate}
              type="text"
              selected={this.state.tgl_akhir}
              onChange={(date) => this.setLastDate(date)}
              label="Tanggal Akhir"
              placeholder="Masukan Tanggal Akhir"
            />
          </div>
          
          <div className="col-lg-2">
            <Field
              name="kode_group"
              component={ReanderSelect}
              options={datagroup}
              onChange={(e)=>this.setState({kode_group : e})}
              label="Kode Group"
              placeholder="Pilih Kode Group"
            />
          </div>
          <div className="col-lg-2">
            <Field
              name="jenis_group"
              component={ReanderSelect}
              options={
                [
                  {
                    value : "ALL",
                    name : "SEMUA",
                  },
                  {
                    value : "MUDA",
                    name : "MUDA",
                  },
                  {
                    value : "TUA",
                    name : "TUA",
                  }
                ]
              }
              label="Jenis Group"
              placeholder="Pilih Jenis Group"
            />
          </div>
          <div className="col-lg-2">
            <div className="form-group">
              <label>&nbsp;</label>
              <br />
              <button
                type="submit"
                value="PDF"
                name="type_btn"
                className="btn btn-primary btn-block"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Mencari
                  </>
                ) : (
                  "Cari Laporan"
                )}
              </button>
            </div>
          </div>
          <div className="col-12">
            <Tabel
              keyField="_id"
              data={this.props.export ? this.props.LaporanDataPenjualanMargin : []}
              columns={this.state.columns}
              empty={true}
              textEmpty="Data Laporan Transaksi Penjualan Margin Kosong"
           
            />
          </div>
          <div className={this.props.export ? "col-lg-12" : "col-lg-12 d-none"}>
            {/* <label htmlFor="">&nbsp;</label> */}
            <div className="row">
              <div className="col-lg-6">
                <button
                  type="button"
                  onClick={() => this.exportPdf()}
                  className="btn btn-warning btn-block"
                >
                  {" "}
                  Export PDF{" "}
                </button>
              </div>
              <div className="col-lg-6">
                <LaporanExelMargin data={this.props.LaporanDataPenjualanMargin} />
              </div>
            </div>
          </div>
        </div>
        {this.props.isLoading ? (
          <Loading
            loading
            background="rgba(0, 0, 0, 0.35)"
            loaderColor="rgba(94, 147, 117, 1)"
          />
        ) : null}
      </form>
    );
  }
}

HeadLaporanMargin = reduxForm({
  form: "HeadLaporanMargin",
  enableReinitialize: true,
  validate: ValidasiTanggalLaporan,
})(HeadLaporanMargin);
export default connect((state) => {
  return {
    GetDataGroup: state.datamaster.GetDataGroup,
    initialValues: {
      tgl_awal: getTglSystem(),
      tgl_akhir: getTglSystem(),
    },
  };
})(HeadLaporanMargin);
