import jsPDF from "jspdf";
import "jspdf-autotable";
import { getToday, getUserdata, localStoragedecryp } from "../../../../../components/helpers/function";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanMarginPdf = (
  data = "",
  type_laporan = "",
  tanggal_dari = "",
  tanggal_sampai = ""
) => {
  // console.log(data);
  // initialize jsPDF
  const doc = new jsPDF("l", "mm", [297, 210]);
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  // let footRows = [];
  let finalY = 30;
  
  let jml_alamat = getUserdata().alamat_toko.length;
  doc.setFontSize(15);
  doc.text("LAPORAN LAPORAN MARGIN", 14, 15);
  doc.setFontSize(20);
  doc.text(getUserdata().nama_toko, 200, 15);
  doc.setFontSize(8);
  if (jml_alamat > 20) {
    doc.text(getUserdata().alamat_toko.slice(0,48), 200, 20);
  }
  if (jml_alamat > 50) {
    doc.text(getUserdata().alamat_toko.slice(48,90), 200, 25);
  }
  doc.setFontSize(10);
  doc.setProperties({
    title: "LAPORAN LAPORAN MARGIN",
  });
  //row 1
  doc.text(
    `PERIODE : ${
      localStoragedecryp("tgl_laporan").length === 0
        ? null
        : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
          " s/d " +
          JSON.parse(localStoragedecryp("tgl_laporan")).tgl_akhir
    }`,
    14,
    25
  );
  let tableColumnSemua = [
    // [
    //   {
    //     content: `No Faktur Group : ${item._id.no_faktur_group}`,
    //     colSpan: 10,
    //   },
    // ],
    [
      {
        content: `KODE BARCODE`,
      },
      {
        content: `NAMA BARANG`,
      },
      {
        content: `KODE GROUP`,
      },
     
      {
        content: `MODAL`,
        styles: {
          halign: 'right',
        },
      },
     
      {
        content: `DIJUAL`,
        styles: {
          halign: 'right',
        },
      },
      {
        content: `MARGIN`,
        styles: {
          halign: 'right',
        },
      },
    ],
  ];
  
 
  
  data.forEach((item, index) => {

    // let data = [
    //   {
    //     content: `No Faktur Group : ${item._id.no_faktur_group}`,
    //     colSpan: "10",
    //   },
    // ];
    // tableRows.push(data);

    // item.pembayaran.forEach((data_pembayaran, index) => {
    //   // console.log(data_pembayaran.jumlah_in);
     
    // })
   
    // item.detail.forEach((item, index) => {
     
      let rows = [
        item.kode_barcode,
        item.nama_barang,
        item.kode_group,
        {
          content: parseInt(item.modal).toLocaleString("kr-KO"),
          styles: {
            halign: 'right',
            textColor: "#000",
          },
        },
       
        {
          content:    parseInt(item.di_jual).toLocaleString("kr-KO"),
          styles: {
            halign: 'right',
            textColor: "#000",
          },
        },
        {
          content:  parseInt(item.margin).toLocaleString("kr-KO"),
          styles: {
            halign: 'right',
            textColor: "#000",
          },
        },
      
      ];

      tableRows.push(rows);
      // });
      
    });
  
  
  let footerSemua = [
    {
      content: `Subtotal`,
      colSpan: 3,
      styles: {
        halign: 'center',
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: `${data.reduce((a,b)=>a + b.modal,0).toLocaleString("kr-Ko")}`,
      styles: {
        halign: 'right',
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: `${data.reduce((a,b)=>a + b.di_jual,0).toLocaleString("kr-Ko")}`,
      styles: {
        halign: 'right',
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: `${data.reduce((a,b)=>a + b.margin,0).toLocaleString("kr-Ko")}`,
      styles: {
        halign: 'right',
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },

  ];
  
  tableRows.push(footerSemua);
  

  let printed = [
    {
      content: `Printed By ${JSON.parse(localStoragedecryp("userdata")).user_id} / ${getToday()}`,
      colSpan: 13,
      styles: {
        // lineWidth: 0.1,
        fontStyle: "italic",
        textColor: "#000",
        // fillColor: "#E8E5E5"
      },
    },
  ]
  tableRows.push(printed);
  
  doc.autoTable({
    head:  tableColumnSemua,
    body: tableRows,
    // foot: footRows,
    startY: finalY,
    theme: "plain",
    rowPageBreak: "avoid",
    // pageBreak: "avoid",
    margin: { top: 10 },
    footStyles: {
      // lineWidth: 0.02,
      // lineColor: "#000",
      fontSize: 8,
    },
    bodyStyles: {
      // lineWidth: 0.02,
      // lineColor: "#000",
      fontSize: 8,
    },
    headStyles: {
      fontSize: 8,
      // lineWidth: 0.02,
      // lineColor: "#000",
      textColor: "#000",
      fillColor: "#E8E5E5",
    },
    tableLineColor: [255, 255, 255],
    tableLineWidth: 1,
  });
  finalY = doc.autoTableEndPosY() + 3;
  tableRows = [];
 
  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }

  doc.save(`LAPORAN LAPORAN MARGIN.pdf`);
  
};

export default LaporanMarginPdf;
