import React, { Component } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import {
  getUserdata,
  localStoragedecryp,
} from "../../../../../components/helpers/function";
class LaporanExelMargin extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <>
        <ReactHTMLTableToExcel
          id="test-table-xls-button"
          className="btn btn-success btn-block"
          table="table-to-xls"
          filename="Laporan Penjualan Margin"
          sheet="Laporan Penjualan Margin"
          buttonText="Export Exel"
        />
        <table id="table-to-xls" style={{ display: "none" }}>
          <thead>
            <tr>
              <th colSpan="7" style={{ textAlign: "center" }}>
                {" "}
                LAPORAN PENJUALAN MARGIN{" "}
              </th>
            </tr>
            <tr>
              <th colSpan="7"> Periode </th>
            </tr>
            <tr>
              <th colSpan="7">
                {" "}
                {localStoragedecryp("tgl_laporan").length === 0
                  ? null
                  : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
                    " s/d " +
                    JSON.parse(localStoragedecryp("tgl_laporan"))
                      .tgl_akhir}{" "}
              </th>
            </tr>
            <tr>
              <th colSpan="7"> TOKO : {getUserdata().nama_toko} </th>
            </tr>

            <tr>
              <th colSpan="7"> ALAMAT : {getUserdata().alamat_toko} </th>
            </tr>

            <tr>
              <th colSpan="7"></th>
            </tr>
            <tr>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                KODE BARCODE
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                NAMA BARANG
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                KODE GROUP
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                MODAL
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                DIJUAL
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                MARGIN
              </td>
            </tr>
          </thead>
          <tbody>
            {this.props.data.map((row, index) => (
              <tr key={index}>
                <td>&nbsp;{row.kode_barcode}</td>
                <td>{row.nama_barang}</td>
                <td>{row.kode_group}</td>
                <td style={{ textAlign: "right" }}> &nbsp;{row.modal.toLocaleString("kr-KO")}</td>
                <td style={{ textAlign: "right" }}>
                  &nbsp;{row.di_jual.toLocaleString("kr-KO")}
                </td>
                <td style={{ textAlign: "right" }}>
                  &nbsp;{row.margin.toLocaleString("kr-KO")}
                </td>
              </tr>
            ))}
          </tbody>
          <tfoot>
            <tr>
              <td
                colSpan="3"
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  textAlign: "center",
                }}
              >
                Subtotal
              </td>
              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  textAlign: "right",
                }}
              >
                {this.props.data.reduce((a,b)=>a + b.modal,0).toLocaleString("kr-Ko")}
              </td>
              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  textAlign: "right",
                }}
              >
                &nbsp;{this.props.data.reduce((a,b)=>a + b.di_jual,0).toLocaleString("kr-Ko")}
              </td>
              <td
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  textAlign: "right",
                }}
              >
                &nbsp;{this.props.data.reduce((a,b)=>a + b.margin,0).toLocaleString("kr-Ko")}
              </td>
             
            </tr>
          </tfoot>
        </table>
      </>
    );
  }
}
export default LaporanExelMargin;
