import {
  React,
  connect,
  Link,
  reduxForm,
  Panel,
  PanelHeader,
  PanelBody,
} from "../../../../components/helpers/library";
import {
  formatDate,
  localStorageencryp,
} from "../../../../components/helpers/function.jsx";
import { ToastNotification } from "../../../../components/helpers/notification.jsx";
import * as moment from "moment-timezone";
import HeadLaporanMargin from "./HeadLaporanMargin";
import { postDataNoTImeOut } from "../../../../config/axios";

class LaporanPenjualanMargin extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      LaporanDataPenjualanMargin: [],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    // this.print = this.print.bind(this);
  }

  handleSubmit(data) {
    // console.log(data);
    this.setState({ isLoading: true });
    let data_post = {
      tgl_awal: formatDate(
        moment(new Date(data.tgl_awal)).format("YYYY-MM-DD")
      ),
      tgl_akhir: formatDate(
        moment(new Date(data.tgl_akhir)).format("YYYY-MM-DD")
      ),
      kode_group: data.kode_group,
      jenis_group: data.jenis_group,
    };

    postDataNoTImeOut("penjualan/reports-margin-penjualan",data_post)
      .then((res) => {
        if (res.data.length === 0) {
          ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
          this.setState({
            isLoading: false,
            export: false,
            LaporanDataPenjualanMargin: [],
          });
        } else {
          ToastNotification("success", "Laporan Transaksi Penjualan Tersedia");
          localStorageencryp("tgl_laporan", JSON.stringify(data_post));
          this.setState({
            LaporanDataPenjualanMargin: [],
          });
          this.setState({
            export: true,
            isLoading: false,
            LaporanDataPenjualanMargin: res.data,
          });
        }
      })
      .catch((err) => {
        ToastNotification("info", "Laporan Transaksi Penjualan Tidak Ada");
        this.setState({
          isLoading: false,
          export: false,
        });
      });
  }

  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan</Link>
          </li>
          <li className="breadcrumb-item active">Laporan Penjualan Margin</li>
        </ol>
        <h1 className="page-header">Laporan Penjualan Margin </h1>
        <Panel>
          <PanelHeader>Laporan Penjualan Margin</PanelHeader>
          <PanelBody>
            <HeadLaporanMargin
            export={this.state.export}
            isLoading={this.state.isLoading}
              onSubmit={(data) => this.handleSubmit(data)}
              LaporanDataPenjualanMargin={this.state.LaporanDataPenjualanMargin}
            />
          </PanelBody>
        </Panel>
      </div>
    );
  }
}

LaporanPenjualanMargin = reduxForm({
  form: "LaporanPenjualanMargin",
  enableReinitialize: true,
})(LaporanPenjualanMargin);
export default connect()(LaporanPenjualanMargin);
